import { Box, Card, CardMedia, Grid, Paper, Typography } from '@mui/material'
import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { setFilmData } from './features/filmSlice'
import { useAppDispatch, useAppSelector } from './hooks/useRedux'
import ListOfFilms from './ListOfFilms'
import LoadingSkeleton from './LoadingSkeleton'
const FilmList = () => {
  const dispatch = useAppDispatch()
  const { filmData } = useAppSelector(state => state.film)
  const [loading, setLoading] = useState<boolean>(true)

  useEffect(() => {
    fetch('https://64b3917c0efb99d862680b90.mockapi.io/FilmData/').then(res => res.json()).then(data => dispatch(setFilmData(data)))
    .catch(error => console.log(error))
    .finally(() => setLoading(false))
  }, []);
  console.log(filmData);

  return (
    <Paper square sx={{ px: { xs: 1, sm: 2, md: 4 }, py: 4, my: 2 }}>
      {loading ?
        <LoadingSkeleton />
        : <>
          <Typography variant='h4' sx={{ pb: 2 }}>Trending</Typography>
          <Grid container spacing={3} >
            {filmData.map((item) => {
              const { id, image, title, year, nation } = item
              return (
                <Grid item key={id} xs={12} sm={6} md={3} lg={2}>
                  <Link to={`detail/${id}`} style={{ textDecoration: 'none' }}>
                    <Card >
                      <CardMedia component="img" image={image} />
                      <Box sx={{ p: 1, bgcolor: 'grey.800' }}>
                        <Typography noWrap gutterBottom sx={{ fontSize: '1.1rem', fontWeight: '700', '&:hover': { color: 'orange' } }}>{title}</Typography>
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', fontWeight: '700' }}>
                          <div>{year}  135 min</div>
                          <div> 9.1</div>
                        </Box>
                      </Box>
                    </Card>
                  </Link >
                </Grid>
              )
            })}
          </Grid>
        </>}
    </Paper>
  )
}

export default FilmList