import { Box, Button, Card, CardMedia, Container, Grid, IconButton, Paper, Table, Typography } from '@mui/material'
import { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { setFilmData } from './features/filmSlice'
import { useAppDispatch, useAppSelector } from './hooks/useRedux'
import ListOfFilms from './ListOfFilms'
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import LinkMUI from '@mui/material/Link';
import AddIcon from '@mui/icons-material/Add';
import LoadingSkeleton from './LoadingSkeleton'
const FilmsManagment = () => {
  const dispatch = useAppDispatch()
  const { filmData } = useAppSelector(state => state.film)
  const [deletedFilm, setDeletedFilm] = useState<string>('')
  const [loading, setLoading] = useState<boolean>(true)
  const navigate = useNavigate()
  useEffect(() => {
    fetch('https://64b3917c0efb99d862680b90.mockapi.io/FilmData/').then(res => res.json()).then(data => dispatch(setFilmData(data)))
      .catch(error => console.log(error))
      .finally(() => setLoading(false))
  }, [deletedFilm]);
  console.log(filmData);
  const deleteFilm = (id: string) => {
    setLoading(true)
    fetch(`https://64b3917c0efb99d862680b90.mockapi.io/FilmData/${id}`, {
      method: 'DELETE',
    }).then(res => {
      if (res.ok) {
        return res.json();
      }
    }).then(film => {
      setDeletedFilm(film.id)
    }).catch(error => {
      console.log(error)
    }).finally(() =>
      setLoading(false)
    )
  }
  const editFilm = (id: string, index: number) => {
    localStorage.setItem('editingFilm', JSON.stringify(filmData[index]))
    navigate(`/edit/${id}`)
  }
  return (
    <Container maxWidth='xl'>
      <Paper square sx={{ px: { xs: 1, sm: 2, md: 4 }, py: 4, my: 2 }}>
        {loading ?
          <LoadingSkeleton />
          : <>
            <Typography variant='h2' sx={{ pb: 2 }}>Manage films</Typography>
            <Box sx={{ display: 'flex', justifyContent: 'start', pb: 2 }}>
              <Button onClick={() => navigate('/add')} variant="outlined" startIcon={<AddIcon />} size='large' color='warning'>
                Add a new film
              </Button>
            </Box>
            <TableContainer >
              <Table sx={{ minWidth: 650 }} aria-label="simple table">
                <TableHead>
                  <TableRow>
                    <TableCell sx={{ fontSize: '1.4rem', fontWeight: 600 }}>ID</TableCell>
                    <TableCell sx={{ fontSize: '1.4rem', fontWeight: 600 }}>Image</TableCell>
                    <TableCell sx={{ fontSize: '1.4rem', fontWeight: 600 }}>Title</TableCell>
                    <TableCell sx={{ fontSize: '1.4rem', fontWeight: 600 }}>Year</TableCell>
                    <TableCell sx={{ fontSize: '1.4rem', fontWeight: 600 }}>Nation</TableCell>
                    <TableCell sx={{ fontSize: '1.4rem', fontWeight: 600 }}>Video</TableCell>
                    <TableCell sx={{ fontSize: '1.4rem', fontWeight: 600 }}>Actions</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {filmData.map((film, index) => (
                    <TableRow
                      key={film.id}
                      sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                    >
                      <TableCell sx={{ fontSize: '1.2rem' }}>{film.id}</TableCell>
                      <TableCell sx={{ fontSize: '1.2rem' }}><img src={film.image} alt="" style={{ width: '4rem' }} /></TableCell>
                      <TableCell sx={{ fontSize: '1.2rem' }}>{film.title}</TableCell>
                      <TableCell sx={{ fontSize: '1.2rem' }}>{film.year}</TableCell>
                      <TableCell sx={{ fontSize: '1.2rem' }}>{film.nation}</TableCell>
                      <TableCell sx={{ fontSize: '1.2rem' }}><LinkMUI href={film.video} target="_blank">{film.video}</LinkMUI></TableCell>
                      <TableCell>
                        <IconButton aria-label="delete" onClick={() => window.confirm('Do you want to delete this film?') && deleteFilm(film.id)}>
                          <DeleteIcon />
                        </IconButton>
                        <IconButton aria-label="edit" onClick={() => editFilm(film.id, index)}>
                          <EditIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </>}
      </Paper>
    </Container>
  )
}

export default FilmsManagment