import { Box, Button, Container, FormControl, FormHelperText, Input, InputLabel, MenuItem, Paper, Select, SelectChangeEvent, TextField, Typography } from '@mui/material'
import React, { useState } from 'react'
import CreateIcon from '@mui/icons-material/Create';
import { useFormik } from 'formik';
import { FilmType } from './features/filmSlice';
import { useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
type NewFilmType = {
  title: string,
  year: number,
  nation: string,
  image: string,
  video: string
}
const AddFilm = () => {

  const currentYear = (new Date).getFullYear()
  const initialFilmValue: NewFilmType = {
    title: '',
    year: currentYear,
    nation: '',
    image: '',
    video: '',
  }
  const navigate = useNavigate()
  const formik = useFormik({
    initialValues: initialFilmValue,
    onSubmit: values => {
      fetch('https://64b3917c0efb99d862680b90.mockapi.io/FilmData', {
        method: 'POST',
        headers: { 'content-type': 'application/json' },
        body: JSON.stringify(values)
      }).then(res => {
        if (res.ok) {
          navigate('/')
        }
      }).catch(error => {
        console.log(error)
      })
    },
    validationSchema: Yup.object().shape({
      title: Yup.string().required("Require"),
      nation: Yup.string().required("Require").min(2, "Please enter a valid nation"),
      year: Yup.number().integer().required("Require").typeError("Please enter a valid number").min(1900),
      image: Yup.string().required("Require"),
      video: Yup.string().required("Require"),
    })
  });


  const { handleSubmit, handleChange, errors, touched, values: { image, title, year, nation, video } } = formik

  return (
    <Container maxWidth='xl' sx={{ height: '56rem' }}>
      <Paper sx={{ display: 'flex', flexDirection: 'column', p: 8, gap: 2, my: 4 }}>
        <Typography variant='h3' sx={{ textAlign: "center" }}>Add new film</Typography>
        <TextField
          label="Title"
          name="title"
          placeholder='Title of the film'
          onChange={handleChange}
        />
        {errors.title && touched.title && (<Typography variant="caption" color="red">{errors.title}</Typography>)}
        <TextField
          label="Year"
          name="year"
          placeholder={currentYear.toString()}
          onChange={handleChange}
        />
        {errors.year && touched.year && (<Typography variant="caption" color="red">{errors.year}</Typography>)}
        <TextField
          label="Nation"
          name="nation"
          onChange={handleChange}
        />
        {errors.nation && touched.nation && (<Typography variant="caption" color="red">{errors.nation}</Typography>)}
        <TextField
          label="Image"
          name="image"
          placeholder='Url'
          onChange={handleChange}
        />
        {errors.image && touched.image && (<Typography variant="caption" color="red">{errors.image}</Typography>)}
        <TextField
          label="Video"
          name="video"
          placeholder='Url'
          onChange={handleChange}
        />
        {errors.video && touched.video && (<Typography variant="caption" color="red">{errors.video}</Typography>)}

        <Button onClick={() => handleSubmit()} variant='contained' sx={{ width: '6rem' }} color='warning'>SUBMIT</Button>
      </Paper>
    </Container >)
}
export default AddFilm