import * as React from 'react';
import ListSubheader from '@mui/material/ListSubheader';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Collapse from '@mui/material/Collapse';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import DraftsIcon from '@mui/icons-material/Drafts';
import SendIcon from '@mui/icons-material/Send';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { Container } from '@mui/system';
import { Paper, Typography } from '@mui/material';
import Films from './ListOfFilms'

const About = () => {
  const [openMovie, setOpenMovie] = React.useState(false);
  const [openNation, setOpenNation] = React.useState(false);
  const [openDailyNews, setOpenDailyNews] = React.useState(false);
  const Nations = ["US", "UK", "Germany", "Japan", "Finland"]
  return (
    <Container maxWidth='xl' sx={{ height: '56rem', my: 4 }}>
      <Paper>
        <List
          sx={{ width: '100%', bgcolor: 'background.paper' }}
          component="nav"
          aria-labelledby="nested-list-subheader"
          subheader={
            <ListSubheader component="div" id="nested-list-subheader">
              About page
            </ListSubheader>
          }
        >
          <ListItemButton onClick={() => setOpenMovie(!openMovie)}>
            <ListItemIcon><InboxIcon /></ListItemIcon>
            <ListItemText primary="THE MOVIES" />{openMovie ? <ExpandLess /> : <ExpandMore />}</ListItemButton>
          <Collapse in={openMovie} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              {Films.map((items) =>
                <ListItemButton sx={{ pl: 4 }}>
                  <Typography>{items.title}</Typography>
                </ListItemButton>
              )}
            </List>
          </Collapse>
          <ListItemButton onClick={() => setOpenNation(!openNation)}>
            <ListItemIcon><DraftsIcon /></ListItemIcon>
            <ListItemText primary="THE NATIONS" />{openNation ? <ExpandLess /> : <ExpandMore />}</ListItemButton>
          <Collapse in={openNation} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              {Nations.map((nation) =>
                <ListItemButton sx={{ pl: 4 }}>
                  <Typography>{nation}</Typography>
                </ListItemButton>
              )}
            </List>
          </Collapse>
          <ListItemButton onClick={() => setOpenDailyNews(!openDailyNews)}>
            <ListItemIcon><SendIcon /></ListItemIcon>
            <ListItemText primary="DAILY NEWS" />{openDailyNews ? <ExpandLess /> : <ExpandMore />}</ListItemButton>
          <Collapse in={openDailyNews} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <ListItemButton sx={{ pl: 4 }}>
                <Typography>Show something...</Typography>
              </ListItemButton>
            </List>
          </Collapse>


        </List>
      </Paper>

    </Container>
  );
}

export default About