import { createSlice, PayloadAction } from '@reduxjs/toolkit'
export type FilmType = {
  id: string,
  image: string,
  title: string,
  year: number,
  nation: string,
  video: string,
}
const initialFilmData: FilmType[] = []
export const filmSlice = createSlice({
  name: 'film',
  initialState: {
    filmData: initialFilmData,
  },
  reducers: {
    setFilmData: (state, action: PayloadAction<FilmType[]>) => {
      state.filmData = action.payload
    },
  }
})

// Action creators are generated for each case reducer function
export const { setFilmData} = filmSlice.actions

export default filmSlice.reducer