import React, { useEffect, useState } from 'react'
import ListOfFilms from './ListOfFilms'
import { useParams } from 'react-router-dom';
import { Button, Grid, Modal, Paper, Typography } from '@mui/material';
import Skeleton from '@mui/material/Skeleton';
import Stack from '@mui/material/Stack';
import { Box, Container } from '@mui/system';
import PlayCircleIcon from '@mui/icons-material/PlayCircle';
import TrailerModal from './TrailerModal';
import { FilmType } from './features/filmSlice';
import { useAppSelector } from './hooks/useRedux';
import LoadingSkeleton from './LoadingSkeleton';
const FilmDetails = () => {

  const { filmId } = useParams()
  const [open, setOpen] = useState<boolean>(false)
  const [film, setFilm] = useState<FilmType | undefined>()
  useEffect(() => {
    window.scrollTo(0, 0)
    fetch(`https://64b3917c0efb99d862680b90.mockapi.io/FilmData/${filmId}`).then(res => res.json()).then(data => setFilm(data))
  }, []);

  const { image, title, year, nation, video } = film || {} as FilmType

  return (
    <Container maxWidth='xl' sx={{ pt: '2rem', pb: '12rem' }}>
      {film ?
        <Grid container spacing={4}>
          <Grid item xs={12} md={8} xl={9}>
            <Box sx={{ height: '40rem', bgcolor: 'background.paper', display: 'flex', justifyContent: 'center', alignItems: 'center', border: "1px black solid", borderRadius: "0.25rem" }}>
              <PlayCircleIcon sx={{ fontSize: '12rem', cursor: 'pointer' }} />
            </Box>
          </Grid>
          <Grid item xs={12} md={4} xl={3}>
            <Paper sx={{ height: '40rem', display: 'flex', flexDirection: 'column', justifyContent: 'space-between', p: '1rem', gap: '0.5rem' }}>
              <Box sx={{ display: 'flex', justifyContent: 'center' }}>
                <img src={image} alt="" height={320} />
              </Box>
              <Typography variant='h5'>{title}</Typography>
              <Typography>Year: {year}</Typography>
              <Typography>Country: {nation}</Typography>
              <Typography>Duration: {135} m</Typography>
              <Typography>Details: ...</Typography>
              <TrailerModal video={video} open={open} setOpen={setOpen} />
            </Paper>
          </Grid>
        </Grid>
        : <LoadingSkeleton />
      }
    </Container >
  )
}

export default FilmDetails