import React, { useState } from 'react'
import { styled, alpha } from '@mui/material/styles';
import SearchIcon from '@mui/icons-material/Search';
import { Input, InputBase } from '@mui/material';
import { useAppDispatch } from './hooks/useRedux';
import { setFilmData } from './features/filmSlice';

const SearchBar = () => {
  const [keyword, setKeyword] = useState('')
  const dispatch = useAppDispatch()
  const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('md')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  }));

  const SearchIconWrapper = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  }));

  const StyledInputBase = styled(InputBase)(({ theme }) => ({
    color: 'inherit',
    '& .MuiInputBase-input': {
      padding: theme.spacing(1, 1, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(4)})`,
      transition: theme.transitions.create('width'),
      width: '100%',
      [theme.breakpoints.up('md')]: {
        width: '20ch',
        // '&:focus': {
        //   width: '20ch',
        // },
      },
    },
  }));
  const handleInput = (e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
    setKeyword(e.target.value.toLowerCase())
  }
  const searchFilms = (e: React.KeyboardEvent<HTMLDivElement>) => {
    if (e.key === 'Enter') {
      const url = new URL('https://64b3917c0efb99d862680b90.mockapi.io/FilmData');
      url.searchParams.append('search', keyword)
      fetch(url).then(res => {
        if (res.ok) {
          return res.json();
        }
      }).then(data =>
        dispatch(setFilmData(data))
      ).catch(error => {
        console.log(error)
      })
    }
  }
  return (
    <Search>
      <SearchIconWrapper>
        <SearchIcon />
      </SearchIconWrapper>
      <StyledInputBase
        placeholder="Search"
        inputProps={{ 'aria-label': 'search' }}
        autoFocus
        value={keyword}
        onChange={(e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => handleInput(e)}
        onKeyDown={(e) => { searchFilms(e as React.KeyboardEvent<HTMLDivElement>) }}
      />
    </Search>
  )
}

export default SearchBar