import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import MenuIcon from '@mui/icons-material/Menu';
import { AppBar, Avatar, Box, Button, Container, IconButton, InputBase, Menu, MenuItem, Toolbar, Tooltip, Typography, useTheme } from '@mui/material';
import { styled } from '@mui/material/styles';
import React, { useContext } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { ColorModeContext } from './ColorModeProvider';
import { logout } from './features/userSlice';
import { useAppDispatch, useAppSelector } from './hooks/useRedux';
import LoginButton from './LoginButton';
import SearchBar from './SearchBar';

const Header = () => {
  const pages = ['about', 'news', 'contact'];
  const navigate = useNavigate()
  const { auth } = useAppSelector(state => state.user)
  const theme = useTheme()
  const dispatch = useAppDispatch()
  const colorMode = useContext(ColorModeContext)

  const [anchorElNav, setAnchorElNav] = React.useState<null | HTMLElement>(null);
  const [anchorElUser, setAnchorElUser] = React.useState<null | HTMLElement>(null);

  const handleOpenNavMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const handleLogout = () => {
    handleCloseUserMenu()
    dispatch(logout())
    navigate('/')
  }
  
  return (
    <Box sx={{ flexGrow: 1, width: 1, height: '4rem' }}>
      <AppBar position="static" sx={{ height: '4rem' }} >
        <Container maxWidth="xl">
          <Toolbar sx={{ p: 0, bgColor: 'green' }}>
            <Typography
              variant="h4"
              noWrap
              sx={{
                mr: 2,
                display: { xs: 'none', md: 'flex' },
                fontFamily: 'monospace',
                fontWeight: 800,
                letterSpacing: { sm: '.3rem' },
                textDecoration: 'none',
              }}
            >
              <NavLink to='/' style={{ textDecoration: 'none', color: 'orange' }}>
                Muvi
              </NavLink>
            </Typography>

            <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
              <IconButton
                size="large"
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleOpenNavMenu}
                color="inherit"
              >
                <MenuIcon />

              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorElNav}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'left',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'left',
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
                sx={{
                  display: { xs: 'block', md: 'none' },
                }}
              >
                {pages.map((page) => (
                  <MenuItem key={page} onClick={handleCloseNavMenu}>
                    <NavLink to={`/${page}`} style={{ textDecoration: 'none', color: 'inherit' }} key={page}>
                      <Typography textAlign="center">{page}</Typography>
                    </NavLink>
                  </MenuItem>
                ))}
              </Menu>
            </Box>
            <Typography
              variant="h4"
              noWrap
              sx={{
                display: { xs: 'flex', md: 'none' },
                flexGrow: 1,
                fontFamily: 'monospace',
                fontWeight: 700,
                letterSpacing: '.1rem',
                textDecoration: 'none',
              }}
            >
              <NavLink to='/' style={{ textDecoration: 'none', color: 'orange' }}>
                Muvi
              </NavLink>
            </Typography>
            
            <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
              {pages.map((page) => (
                <NavLink to={`/${page}`} style={{ textDecoration: 'none', color: 'text.primary' }} key={page}>
                  <Button
                    onClick={handleCloseNavMenu}
                    sx={{ m: 2, color: 'white', display: 'block' }}
                  >
                    {page}
                  </Button>
                </NavLink>
              ))}
            </Box>
            <Tooltip title='Toggle theme mode' >
              <IconButton sx={{ mx: 1, border: '1px' }} onClick={colorMode.toggleColorMode} color="inherit">
                {theme.palette.mode === 'dark' ? <Brightness7Icon /> : <Brightness4Icon />}
              </IconButton>
            </Tooltip>
            <Box sx={{ px: 2 }}>
              <SearchBar />
            </Box>
            {auth.email ?
              <Box sx={{ flexGrow: 0 }}>
                <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                  <Avatar alt="user avatar" src={auth?.picture} />
                </IconButton>
                <Menu
                  sx={{ mt: '45px' }}
                  id="menu-appbar"
                  anchorEl={anchorElUser}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  open={Boolean(anchorElUser)}
                  onClose={handleCloseUserMenu}
                >
                  <MenuItem onClick={handleCloseUserMenu}>
                    <Typography textAlign="center">Profile</Typography>
                  </MenuItem>
                  <MenuItem onClick={() =>{ navigate('/dashboard'); handleCloseUserMenu() }}>
                    <Typography textAlign="center">Dashboard</Typography>
                  </MenuItem>
                  <MenuItem onClick={handleLogout}>
                    <Typography textAlign="center">Logout</Typography>
                  </MenuItem>
                </Menu>
              </Box>
              :
              <LoginButton />
            }
          </Toolbar>
        </Container>
      </AppBar>
    </Box >
  )
}

export default Header