import { Button, Container, Paper, TextField, Typography } from '@mui/material';
import { useFormik } from 'formik';
import { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import * as Yup from 'yup';
import { FilmType } from './features/filmSlice';
import LoadingSkeleton from './LoadingSkeleton';

const EditFilm = () => {
  const { filmId } = useParams()
  const [film, setFilm] = useState<FilmType>(JSON.parse(localStorage.getItem('editingFilm') as string))
  console.log(film);

  useEffect(() => {
    window.scrollTo(0, 0)
    fetch(`https://64b3917c0efb99d862680b90.mockapi.io/FilmData/${filmId}`).then(res => res.json()).then(data => setFilm({ ...data }))
  }, []);
  const navigate = useNavigate()
  const formik = useFormik({
    initialValues: film,
    onSubmit: values => {
      fetch(`https://64b3917c0efb99d862680b90.mockapi.io/FilmData/${filmId}`, {
        method: 'PUT',
        headers: {'content-type':'application/json'},
        body: JSON.stringify(values)
      }).then(res => {
        if (res.ok) {
          localStorage.removeItem('editingFilm')
          navigate('/dashboard')
        }
      }).catch(error => {
        console.log(error)
      })
    },
    validationSchema: Yup.object().shape({
      title: Yup.string().required("Require"),
      nation: Yup.string().required("Require").min(2, "Please enter a valid nation"),
      year: Yup.number().integer().required("Require").typeError("Please enter a valid number").min(1900),
      image: Yup.string().required("Require"),
      video: Yup.string().required("Require"),
    })
  });

  const { handleSubmit, handleChange, errors, touched, values: { image, title, year, nation, video } } = formik

  return (
    <Container maxWidth='xl' sx={{ height: '48rem' }}>
      {film ?
        <Paper sx={{ display: 'flex', flexDirection: 'column', p: 8, gap: 2, my: 4 }}>
          <Typography variant='h3' sx={{ textAlign: "center" }}>Edit film at id: {filmId}</Typography>
          <TextField
            label="Title"
            name="title"
            value={title}
            onChange={handleChange}
          />
          {errors.title && touched.title && (<Typography variant="caption" color="red">{errors.title}</Typography>)}
          <TextField
            label="Year"
            name="year"
            value={year}
            onChange={handleChange}
          />
          {errors.year && touched.year && (<Typography variant="caption" color="red">{errors.year}</Typography>)}
          <TextField
            label="Nation"
            name="nation"
            value={nation}
            onChange={handleChange}
          />
          {errors.nation && touched.nation && (<Typography variant="caption" color="red">{errors.nation}</Typography>)}
          <TextField
            label="Image"
            name="image"
            value={image}
            onChange={handleChange}
          />
          {errors.image && touched.image && (<Typography variant="caption" color="red">{errors.image}</Typography>)}
          <TextField
            label="Video"
            name="video"
            value={video}
            onChange={handleChange}
          />
          {errors.video && touched.video && (<Typography variant="caption" color="red">{errors.video}</Typography>)}
          <Button onClick={() => handleSubmit()} variant='contained' sx={{ width: '6rem' }} color='warning'>SUBMIT</Button>
        </Paper>
        : <LoadingSkeleton />
      }
    </Container >)
}
export default EditFilm