import React, { useState, useEffect } from 'react';
import { googleLogout, useGoogleLogin, GoogleOAuthProvider } from '@react-oauth/google';
import { Button, useTheme } from '@mui/material';
import { useDispatch } from 'react-redux';
import { login, logout, UserState } from "./features/userSlice";
import { useAppDispatch } from './hooks/useRedux';

function Login() {
    const dispatch = useAppDispatch()
    const theme = useTheme()

    const [user, setUser] = useState<any>(null);
    const loginWithGoogle = useGoogleLogin({
        onSuccess: (codeResponse) => setUser(codeResponse),
        onError: (error) => console.log('Login Failed:', error)
    });

    useEffect(
        () => {
            if (user) {
                fetch(`https://www.googleapis.com/oauth2/v1/userinfo?access_token=${user.access_token}`, {
                    headers: {
                        Authorization: `Bearer ${user.access_token}`,
                        Accept: 'application/json'
                    }
                })
                    .then((res) => res.json())
                    .then((data) => { dispatch(login(data)) })
                    .catch((err) => console.log(err));
            }
        },
        [user]
    );

    return (
        <Button onClick={() => loginWithGoogle()} variant={theme.palette.mode === 'dark' ? 'outlined' : 'contained'} color='warning' > Login</Button >
    );

}
export default Login;
