import React from 'react'
import { Box, Button, Grid, Modal, Paper, Typography } from '@mui/material';
interface TrailerModelProps {
  video: string,
  open: boolean,
  setOpen: React.Dispatch<React.SetStateAction<boolean>>
}
const TrailerModal = ({ video, open, setOpen }: TrailerModelProps) => {
  return (
    <div>
      <Button variant='contained' color='error' onClick={() => setOpen(true)} fullWidth>Watch trailer</Button>
      <Modal
        open={open}
        onClose={() => setOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <iframe width={1020} height={630} src={video}
          style={{ position: 'fixed', left: '50%', top: '50%', transform: 'translate(-50%, -50%)', width: '70%', height: '70%' }}
          title="YouTube video player"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
          allowFullScreen>
        </iframe>
      </Modal>
    </div >
  )
}

export default TrailerModal