import { useLocation, Navigate, Outlet } from 'react-router-dom'
import React from 'react'
import { useAppSelector } from './hooks/useRedux'

const RequireAuth = () => {
  const { auth } = useAppSelector(state => state.user)
  return (
    auth.email
      ? <Outlet />
      : <Navigate to='/' replace />
  )
}

export default RequireAuth