import { Box, Button, Container, FormControl, FormHelperText, Input, InputLabel, MenuItem, Paper, Select, SelectChangeEvent, TextField, Typography } from '@mui/material'
import React, { useState } from 'react'
import CreateIcon from '@mui/icons-material/Create';
const Contact = () => {
  const [nation, setNation] = useState("")
  const handleChange = (event: SelectChangeEvent) => {
    setNation(event.target.value as string);
  };
  return (
    <Container maxWidth='xl' sx={{ height: '56rem' }}>
      <Paper sx={{ display: 'flex', flexDirection: 'column', p: 8, gap: 6, my: 8 }}>
        <Typography variant='h3' sx={{textAlign:"center"}}>Contact us</Typography>
        <FormControl>
          <InputLabel htmlFor="name">Full name</InputLabel>
          <Input id="name" />
        </FormControl>
        <FormControl>
          <InputLabel htmlFor="phone">Phone number</InputLabel>
          <Input id="phone" />
        </FormControl>
        <FormControl>
          <InputLabel htmlFor="email">Email address</InputLabel>
          <Input id="email" />
        </FormControl>
        <FormControl fullWidth>
          <InputLabel id="nation">Nation</InputLabel>
          <Select
            labelId="nation"
            id="nationSelect"
            value={nation}
            label="Age"
            onChange={handleChange}
          >
            <MenuItem value="US">US</MenuItem>
            <MenuItem value="Japan">Japan</MenuItem>
            <MenuItem value="Germany">Germany</MenuItem>
          </Select>
        </FormControl>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <CreateIcon fontSize='large' />
          <FormControl sx={{ width: '100%' }}>
            <InputLabel htmlFor="content">Your Content</InputLabel>
            <Input id="content" fullWidth />
          </FormControl>
        </Box>
        <Button variant='contained' sx={{ width: '6rem' }} color='success'>SUBMIT</Button>
      </Paper>
    </Container>)
}
export default Contact